.row{
    height: 100vh;
    padding-left: 5%;
}
.text{
    padding-top: 8%;
}
.text h1{
    font-size: 80px;
    font-weight: bolder;
}
.text p{
    margin-top: 5%;
}
.buttons{
    margin-top: 5%;
}
.buttons .start{
    padding: 12px 28px;
    border-radius: 30px;
    background-color: blue;
    color: white;
    margin-right: 30px;
}
.buttons .video{
    color: blue;
    font-size: 18px;
}
.brand{
    margin-top: 7%;
}
.brand .logo{
    width: 90px;
    height: 50px;
    padding-right: 20px;
}
.logo2{
    width: 90px;
    height: 50px;
    padding-right: 20px;
    margin-top: 20px;
}
.image img{
    width: 600px;
    height: 600px;
}
